    grid-template-columns: repeat(3, 1fr) !important;
    gap: 16px !important;
    padding: 0 !important;
  }
  .sheets-grid .file-card {
    margin: 0 !important;
  }
  .files-grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
