  transform: translateY(-1px);
}
.btn-danger-hover:active:not(:disabled) {
  transform: translateY(0);
}
.btn-success-hover {
  transition: all 0.2s ease;
}
.btn-success-hover:hover:not(:disabled) {
  background: rgba(34, 197, 94, 0.2) !important;
