  transform: translateY(-1px);
}
.btn-success-hover:active:not(:disabled) {
  transform: translateY(0);
}
.btn-info-hover {
  transition: all 0.2s ease;
}
.btn-info-hover:hover:not(:disabled) {
  background: rgba(52, 152, 219, 0.2) !important;
  border-color: #3498db !important;
  transform: translateY(-1px);
}
.btn-info-hover:active:not(:disabled) {
  transform: translateY(0);
}
.btn-purple-hover {
  transition: all 0.2s ease;
}
.btn-purple-hover:hover:not(:disabled) {
  background: rgba(147, 51, 234, 0.2) !important;
  border-color: #9333EA !important;
  transform: translateY(-1px);
}
.btn-purple-hover:active:not(:disabled) {
  transform: translateY(0);
}
.list-item-hover {
  transition: all 0.15s ease;
}
.list-item-hover:hover {
  background: var(--bg-secondary);
  transform: translateX(4px);
}
.list-item-highlight-hover {
